import React from 'react';
import Header from './container/header';
import {HashRouter as Router,Route} from 'react-router-dom';
import Home from './container/home';
import Api from './components/header/Api';
import Start from './components/header/Start';
import About from './components/header/About';
import Footer from './container/footer';
import Login from './components/Login/login';
import Sidertop from './components/Sider/top';
function App() {
  return (
    <Router>
        <div className="App">
          <Header/>
        </div>
        <div className='main'>
            <div className='content'>
              <Route path='/home' component={Home}/>
              <Route path='/getstart' component={Start}/>
              <Route path='/api' component={Api}/>
              <Route path='/about' component={About}/>
              <Route path='/login' component={Login}/>
            </div>
            <div className='sider'>
              <div className='sider_top'>
                <Route path='/home' component={Sidertop}/>
              </div>
              <div className='sider_middle'>
              </div>
              <div className='sider_bottom'>
              </div>
            </div>
            <div className='footer'>
              <Footer/>
            </div>
        </div>

    </Router>
  );
}
export default App;
