import React from 'react';
import {Link} from 'react-router-dom';
export default function () {
    let link = {
        textDecoration:'none', outLine: 'none',color: 'white'
    }
    return (
        <div className = 'headerwrap'>
            <header>
                <img src = 'https://static2.cnodejs.org/public/images/cnodejs_light.svg'/>
                <input type='text'/>
                <div style={{float:'right'}} >
                    <ul>
                        <li><Link style={link} to={'/home/all/1'}><span>首页</span></Link></li>
                        <li><Link style={link} to={'/getstart'}><span>新手入门</span></Link></li>
                        <li><Link style={link} to={'/api'}><span>API</span></Link></li>
                        <li><Link style={link} to={'/about'}><span>关于</span></Link></li>
                        <li><Link style={link} to={'/join'}><span>注册</span></Link></li>
                        <li><Link style={link} to={'/login'}><span>登录</span></Link></li>
                    </ul>
                </div>
            </header>
        </div>)
}
