import React, { Component } from 'react'
import {Link} from 'react-router-dom';
import {Route} from 'react-router-dom';
import All from '../components/All/All';
import Good from '../components/Nav/Good';
import Share from '../components/Nav/Share';
import Ask from '../components/Nav/Ask';
import Job from '../components/Nav/Job';
export default class Home extends Component {

    render() {
        let link = {
            textDecoration:'none', outLine: 'none',color: 'white'
        }
        return (
            <div>
                <div className='nav'>
                    <ul>
                        <li><Link to={'/home/all/1'} style={link}>全部</Link></li>
                        <li><Link to={'/home/good/1'} style={link}>精华</Link></li>
                        <li><Link to={'/home/share/1'} style={link}>分享</Link></li>
                        <li><Link to={'/home/ask/1'} style={link}>回答</Link></li>
                        <li><Link to={'/home/job/1'} style={link}>招聘</Link></li>
                    </ul>
                </div>
                <div>
                    <Route path='/home/all/:pages' component = {All}/>
                    <Route path='/home/good/:pages' component = {Good}/>
                    <Route path='/home/share/:pages' component = {Share}/>
                    <Route path='/home/ask/:pages' component = {Ask}/>
                    <Route path='/home/job/:pages' component = {Job}/>
                </div>
            </div>
        )
    }
}
