import React, { Component } from 'react'
import Pages from './Pages'

export default class All extends Component {

    constructor(){
        super();
        this.state = {
            data:[]
        }
    }
    getData = ()=>{
        fetch(`https://cnodejs.org/api/v1/topics?page=${this.props.match.params.pages}`).then(req=>req.json()).then(data=>{
            // console.log(data);
            this.setState({
                data:data.data
            })
        });
        // console.log(this.props.match);
    }
    title = (title)=>{
        let str;
        if(title.length>=35){
            str = title.substr(0,35)+'···';
            return str;
        }
        else{
            return title;
        }
    }
    componentDidUpdate(prevProps){
        // console.log(prevProps);
        if(prevProps.match.params.pages !== this.props.match.params.pages)
            this.getData();
    }
    //渲染之前先请求一次，防止首次加载第一页出不来
    componentWillMount(){
        this.getData();
    }
    render() {
        let cell = {
            width:"100%",
            height:'50px',
            borderBottom:"1px solid rgb(184, 184, 184)",
            borderTop:'1px solid rgb(184, 184, 184)'
        }
        let img = {
            width:"40px",
            marginTop:'5px',
            marginLeft:'5px',
            float:'left'
        }
        let link = {
            textDecoration:'none', outLine: 'none',color: 'black'
        }
        let visit = {
            float:'left',
            marginTop:'20px',
            fontSize:'13px',
            marginLeft:'10px',
            width:'40px'
        }
        let tab = {
            ask:"问答",
            share:"分享",
            job:"招聘"
        };
        return (
            <div className="content_main">
                <ul>{this.state.data.map((item,index)=>{
                    if(item.tab === 'job')
                    console.log(item.tab);
                        return (
                            <li key={index}>
                                <div style={cell}>
                                    <a href={`https://cnodejs.org/user/${item.author.loginname}`}><img style={img} src={item.author.avatar_url} alt={item.author.loginname}/></a>
                                    <span style={visit}>{`${item.reply_count}/${item.visit_count}`}</span>
                                    <span className="tab">{tab[item.tab]}</span>
                                    <a href={`https://cnodejs.org/topic/${item.id}`} style={link}><span className="title">{this.title(item.title)}</span></a>
                                    
                                </div>
                            </li>
                        )
                    })}
                </ul>
                <Pages type = {'all'}/>
            </div>
        )
    }
    
}
