import React, { Component } from 'react';
import {fetchuserInfor} from '../../redux/action/createAction';
import store from '../../redux/store/index';
export default class Login extends Component {
    render() {
        return ( 
            <div>
                <div className = 'login'>
                    <div>
                    <span className='text'>用户名</span><input type='text' name='username' className='user'/>
                    </div>
                    <div className='login_pwd'>
                    <span className='text'>密码</span><input type='password' name='pwd' className='pwd'/>
                    </div>
                    <input type='button' value='登录' className='submit' onClick={()=>{this.login()}}/>
                </div>
            </div>
        )
    }
    login = ()=>{
        fetch('https://cnodejs.org/api/v1/user/alsotang').then(req=>req.json()).then(data=>{
            store.dispatch(fetchuserInfor(data));
            window.location.hash = '#/home/all/1';
        });
    }
}