import React, { Component } from 'react'

export default class Footer extends Component {
    render() {
        let style = {
            backgroundColor:'gray',
            width:'100%',
            float:'left',
            marignTop:'2000px',
            height:'200px',
            textAlign:'center',
            fontSize:'50px',
            lineHeight:'200px',
            color:'white'
        }
        return (
            <div>
                <div style={style}>this is footer</div>
            </div>
        )
    }
}
