import {combineReducers} from 'redux';
let userInfor = {
    avatar_url:'',
    login_name:''
};

function getUserInfor(state=userInfor,action) {
    // console.log(action.type);
    switch (action.type) {
        case 'fetch_user_data':{
            userInfor.avatar_url = action.userData.data.avatar_url;
            userInfor.login_name = action.userData.data.loginname;
            return state;
        }
        default:
            return state;
    }
}

export default combineReducers({
    getUserInfor
});