import React, { Component } from 'react'
import store from '../../redux/store/index';
export default class top extends Component {
    constructor(){
        super();
        this.state={
            user:{
                avatar_url:'',
                login_name:''
            }
        }
    }
    componentDidMount(){
        this.con();
    }
    componentDidUpdate(prevProps){
        if(prevProps !== this.props)
            this.con();
    }
    con = ()=>{
        // console.log(store.getState().getUserInfor);
        this.setState({
            user:{
                avatar_url:store.getState().getUserInfor.avatar_url,
                login_name:store.getState().getUserInfor.login_name
            }
        });
    }
    render() {
        return (
            <div style={{textAlign:'center',width:'50px',float:'left',marginLeft:'30%'}}>
                <img src={this.state.user.avatar_url} width='80px'/>
                <span>{this.state.user.login_name}</span>
                {/* <button onClick={this.con}>查看</button> */}
            </div>
        )
    }

}
