import React, { Component } from 'react';
import {Link} from 'react-router-dom';

export default class Pages extends Component {
    constructor(){
        super();
        this.state = {
            li:[1,2,3,4,5,6,7]
        }
    }
    href = (type)=>{
        let num = parseInt(window.location.href.split('/')[6]);
        // console.log(window.location.href.split('/'));
        if(type === 'add')
            return `/home/${this.props.type}/`+(num+1);
        else if(type === 'min'){
            if(num === 1){
                return `/home/${this.props.type}/1`;
            }
            return `/home/${this.props.type}/` +(num-1);
        }
    }
    open = ()=>{
        for(let i = 0;i<this.state.li.length;i++){
            if(this.state.li[i]>=695) return;
            this.state.li[i] = this.state.li[i] + this.state.li.length;
        }
        // console.log(this.state.li);
        this.setState({
            li:this.state.li
        });
    }
    back = async ()=>{
        for(let i = 0;i<this.state.li.length;i++){
            if(this.state.li[i]<8) return;
            this.state.li[i] = this.state.li[i] - this.state.li.length;
        }
        await this.setState({
            li:this.state.li
        });
    }
    pages = ()=>{
        let link = {
            textDecoration:'none', outLine: 'none',color: 'black'
        }
        let li = {
            cursor: 'pointer',

        }
        return (
            <div>
                <li style = {li} onClick = {this.back}>{'<<'}</li>
                <Link style={link} to={this.href('min',this.props.type)}><li style = {li}>{'<'}</li></Link>
                {
                this.state.li.map((item,index)=>{
                    return (<Link style={link} to={`/home/${this.props.type}/`+item}  key={index}><li>{item}</li></Link>);
                })}
                <Link style={link} to={this.href('add',this.props.type)}><li style = {li}>{'>'}</li></Link>
                <li style = {li} onClick={this.open}>{'>>'}</li>
            </div>
        )
    }
    render() {
        return (
            <div className="pages">
                <ul>{
                    this.pages()
                }
                </ul>
            </div>
        )
    }
}
